import {
  animate,
  state,
  style,
  transition,
  trigger,
} from "@angular/animations";
import { AfterViewInit, Component } from "@angular/core";

@Component({
  selector: "app-splash",
  templateUrl: "./splash.page.html",
  styleUrls: ["./splash.page.scss"],
  animations: [
    trigger("title", [
      state(
        "pristine, void",
        style({
          transform: "translateY(calc(50vh - 3rem))",
          opacity: "0",
        }),
      ),
      state(
        "loaded",
        style({
          transform: "none",
          opacity: "1",
        }),
      ),
      transition("pristine => loaded", animate("1.5s 1s ease-in-out")),
    ]),
    trigger("backdrop", [
      state(
        "pristine, void",
        style({
          transform: "translateY(calc(50vh - 3rem))",
          opacity: "0",
        }),
      ),
      state(
        "loaded",
        style({
          transform: "translateY(1px)",
          opacity: "1",
        }),
      ),
      transition("pristine => loaded", animate("1.5s 1.5s ease-in-out")),
    ]),
  ],
})
export class SplashPage implements AfterViewInit {
  public state = "pristine";

  public constructor() {}

  public ngAfterViewInit(): void {
    this.state = "loaded";
  }
}
